<template>
    <div
            :style="$vuetify.breakpoint.smAndDown ? `height: calc(100vh - 110px)` : ``"
           class="container-fixed">
        <v-card
                class="pa-5 card-header-order"
                outlined
                tile
        >
            <v-row no-gutters>
                <v-col class="text-left">
                    <v-icon>mdi-settings</v-icon>
                    <strong> ຕັ້ງຄ່າ</strong>
                </v-col>
                <v-col class="text-center">
                    <v-icon>mdi-chat-processing</v-icon>
                    <strong> ຂໍ້ຄວາມ</strong>
                </v-col>
                <v-col class="text-right">

                    <v-menu
                            bottom
                            offset-y
                    >
                        <template v-slot:activator="{ on }">
                            <strong
                            >{{ taskAll ? 'ທັງໝົດ' :  ( taskWaiting ? 'ລໍຖ້າຊໍາລະ' : ( taskWorking ?
                                'ກໍາລັງເຮັດ' : (
                                taskPending ? 'ລໍອະນຸມັດ' : ( taskSuccessful ? 'ສໍາເລັດ' : 'ບໍ່ສໍາເລັດ' ) )
                                )) }}
                            </strong>
                            <v-icon   v-on="on">mdi-filter-menu</v-icon>
                        </template>
                        <v-list style="cursor: pointer" >
                            <v-list-item @click="taskAllFunc">
                                <v-list-item-title>ທັງໝົດ</v-list-item-title>
                            </v-list-item>
                            <v-divider></v-divider>
                            <v-list-item  @click="taskWaitingFunc">
                                <v-list-item-title>ລໍຖ້າຊໍາລະ</v-list-item-title>
                            </v-list-item>
                            <v-divider></v-divider>
                            <v-list-item  @click="taskWorkingFunc">
                                <v-list-item-title>ກໍາລັງເຮັດ</v-list-item-title>
                            </v-list-item>
                            <v-divider></v-divider>
                            <v-list-item  @click="taskPendingFunc">
                                <v-list-item-title>ລໍອະນຸມັດ</v-list-item-title>
                            </v-list-item>
                            <v-divider></v-divider>
                            <v-list-item  @click="taskSuccessfulFunc">
                                <v-list-item-title>ສໍາເລັດ</v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-menu>
                </v-col>
            </v-row>
        </v-card>
        <div class="card body-fixed">
            <app-order-loading v-if="orderLoading"></app-order-loading>
               <v-card
                       v-else
                       v-for="order in filterChat" :key="order.index"
                       :to="{ name: 'MessageFocus', params: { id: order._id } }"
                       @click="messageMobileFunc"
                       class="pa-2 card-body-order"
                       outlined
                       tile
               >
                       <v-row no-gutters>
                           <v-col cols="3">
                               <v-avatar  height="48" width="48">
                                   <v-img class="ml-4 mt-5"
                                          :src="
                                          auth._id === order.freelancerId._id ?
                                          $server + order.employerId.image :
                                           $server + order.freelancerId.image"></v-img>
                               </v-avatar>
                           </v-col>
                           <v-col cols="9">
                                  <b>
                                      {{
                                      auth._id === order.freelancerId._id
                                      ? order.employerId.username :
                                      order.freelancerId.username
                                      }}
                                  </b>
                               <small>#{{ order.quotation.orderId }}</small>
                              <v-spacer/>
                                  <small>ອ່ານຂໍ້ຄວາມ...</small>
                               <v-spacer/>
                                  <small>#{{ order.portfolioId.title }}</small>
                           </v-col>
                       </v-row>
               </v-card>
            <div  v-if=" !orderLoading && filterChat.length <= 0" outlined class="pa-2 card-body-order">
                <v-card-actions class="justify-center">
                    <v-icon class="mr-1" size="38">mdi-chat-alert</v-icon>
                    <h4> ບໍ່ພົບຂໍ້ຄວາມ</h4>
                </v-card-actions>
            </div>
           </div>
    </div>
</template>

<script>
    import appOrderLoading from '../loading/message/OrdersLoading'
    export default {
        components: {
          appOrderLoading
        },
        props: [
            'orders',
            'auth',
            'currentOrder',
            'fetchOrders',
            'messageMobileFunc',
            'orderLoading'
        ],
        data: () => ({
            taskAll: true,
            taskWaiting: false,
            taskWorking: false,
            taskPending: false,
            taskSuccessful: false,

            paymentStatus: null,
            complete: false,
        }),
        computed: {
            filterChat () {
                return this.orders.filter((order) => {
                    if(this.taskAll) {
                        return order
                    } else if(this.taskWaiting) {
                        return order.quotation.workStatus === 'wait for payment'
                    } else if(this.taskWorking) {
                        return order.quotation.workStatus === 'in progress'
                    } else if (this.taskPending) {
                        return order.quotation.workStatus === 'pending'
                    } else if (this.taskSuccessful) {
                        return order.quotation.workStatus === 'successful'
                    }
                })
            }
        },
        methods: {
            taskAllFunc () {
                this.taskAll = true
                this.taskWaiting = false
                this.taskWorking = false
                this.taskSuccessful = false
            },
            taskWaitingFunc () {
                this.taskWaiting = true
                this.taskAll = false
                this.taskWorking = false
                this.taskSuccessful = false
            },
            taskWorkingFunc () {
                this.taskWorking = true
                this.taskAll = false
                this.taskWaiting = false
                this.taskSuccessful = false
            },
            taskPendingFunc () {
                this.taskPending = true
                this.taskAll = false
                this.taskWaiting = false
                this.taskWorking = false
                this.taskSuccessful = false
            },
            taskSuccessfulFunc () {
                this.taskSuccessful = true
                this.taskAll = false
                this.taskWaiting = false
                this.taskWorking = false
                this.taskPending = false
            }
        }
    }
</script>

